.c-lm {
  margin: 10px;
  padding: 8px 10px;
  align-items: center;
  justify-content: center;
  background-color: #eceff1;
  display: flex;
  border-radius: 20px;
  cursor: pointer;
  font-size: 14px;
  color: #666;
}
.c-lm > span {
  margin-left: 4px;
}
.c-lm__model-search {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 2;
  padding: 24px;
  background-color: #fff;
}
.c-lm__model-linkman {
  padding: 0 24px;
}
.c-lm__model .ant-modal-body {
  padding: 0;
  transition: all 0.5s;
  overflow: auto;
  position: relative;
}
.c-lm__model .ant-modal-body::-webkit-scrollbar {
  width: 0 !important;
}
