.c-item {
  position: relative;
  width: 100%;
  padding: 12px 16px 16px;
  display: flex;
  cursor: pointer;
}
.c-item:after {
  position: absolute;
  bottom: 0;
  display: block;
  height: 1px;
  right: 16px;
  left: 16px;
  content: '';
  background-color: var(--light);
}
.c-item_active,
.c-item:hover {
  margin-top: -1px;
  margin-left: -1px;
  padding-top: 13px;
  border: 0;
  color: var(--white);
  background-color: var(--blue);
  width: calc(100% + 2px);
}
.c-item_active:after,
.c-item:hover:after {
  display: none;
}
.c-item_active span,
.c-item:hover span {
  color: var(--white);
  background: transparent;
}
.c-item_active .ant-btn .c-item__validation-btns-refuse,
.c-item:hover .c-item__validation-btns-refuse span {
  color: #000;
  background: transparent;
}
.c-item_active .c-item__session-time,
.c-item:hover .c-item__session-time {
  color: var(--white);
  background: transparent;
}
.c-item__session {
  display: flex;
  width: 100%;
}
.c-item__session img {
  float: left;
  width: 40px;
  height: 40px;
  margin-right: 12px;
  border-radius: 50%;
}
.c-item__session-main {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  justify-content: space-between;
  padding: 7px 0px 7px 5px;
  overflow: hidden;
}
.c-item__session-name {
  font-size: 14px;
  line-height: 22px;
  color: var(--dark);
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 600;
}
.c-item__session-preview {
  font-size: 12px;
  display: inline-block;
  overflow: hidden !important;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: var(--grey);
}
.c-item__session-time {
  font-size: 12px;
  padding: 0 0 5px 5px;
  color: var(--grey);
  display: flex;
  word-wrap: break-word;
  align-items: center;
}
.c-item__validation {
  display: flex;
  width: 100%;
}
.c-item__validation-icon {
  position: relative;
  margin-right: 12px;
}
.c-item__validation-icon svg {
  width: 50px;
  height: 50px;
}
.c-item__validation-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex: 1 1;
}
.c-item__validation-wrap-name {
  font: optional;
}
.c-item__validation-btns {
  display: flex;
  flex-direction: column;
}
.c-item__validation-btns-refuse:hover {
  color: var(--blue);
}
.c-item__validation-btns-agree {
  margin-bottom: 5px;
}
.c-item__validation-btns-agree:hover {
  color: var(--blue);
}
.c-item__list-add {
  display: flex;
  align-items: center;
}
.c-item__list-add span {
  white-space: nowrap;
  background: var(--blue);
  color: #fff;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 6px;
}
.c-item__group-checkbox {
  display: flex;
  align-items: center;
}
.c-chat-vd__item-dot {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(50%);
  border-radius: 50%;
  font-size: 12px;
}
