.c-chat-space {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.c-chat-space__avatar {
  cursor: pointer;
  width: 100px;
  height: 100px;
  position: relative;
}
.c-chat-space__avatar svg {
  width: 100%;
  height: 100%;
}
.c-chat-space__avatar-reset {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateX(50%);
  width: 26px;
  height: 26px;
}
.c-chat-space__avatar-edit {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateX(50%);
  width: 26px;
  height: 26px;
}
.c-chat-space__ca {
  flex: 1 1;
}
.c-chat-space__form {
  margin-top: 30px;
}
