.c-group__content-item {
  display: flex;
  padding: 4px 12px 8px;
  align-items: center;
  cursor: pointer;
}
.c-group__content-item-icon {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  font-size: 26px;
  line-height: 30px;
}
.c-group__content-item-des {
  flex: 1 1;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
