.c-home {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.c-home__header {
  padding: 16px 24px;
  border-bottom: 1px solid #f0f0f0;
  border-radius: 2px 2px 0 0;
}
.c-home__body {
  flex-grow: 1;
  padding: 24px;
  font-size: 14px;
  line-height: 1.5715;
  word-wrap: break-word;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.c-home__body-content {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
}
.c-home__body-content-wrap {
  height: 32px;
  font-size: 14px;
  display: flex;
  line-height: 32px;
}
.c-home__body-content-key {
  width: 100px;
  text-align: right;
}
.c-home__body-content-value {
  padding-left: 10px;
}
