.c-header {
  display: flex;
  align-items: center;
  padding: 10px 16px 0 16px;
}
.c-header__avatar {
  width: 50px;
  cursor: pointer;
  height: 50px;
}
.c-header__avatar svg {
  width: 100%;
  height: 100%;
}
.c-header__name {
  flex: 1 1;
  padding-left: 10px;
}
.c-header__more {
  display: flex;
  cursor: pointer;
  padding: 10px 0 10px 10px;
}
.c-header__more li {
  background-color: #333;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  margin-left: 4px;
}
.c-header__more-menu {
  cursor: pointer;
}
.c-header__more-menu p:hover {
  color: #111;
  transform: scale(1.1);
}
.c-header__more-menu p {
  line-height: 26px;
}
