.c-ca__content {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.c-ca__content > label {
  margin-top: 10px;
  display: flex;
}
.c-ca__content > label:nth-child(2n - 1) > span {
  margin-left: 10px;
  order: 3;
}
.c-ca__content > label:nth-child(2n) > span {
  margin-right: 10px;
}
.c-ca__btns {
  flex: 1 1;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  margin-top: 30px;
}
